.container{
    display: flex;
    background: rgb(239, 239, 244);
    .left{
        height: 80%;
        width: 65%;
        .upper{
            border-right:1px solid grey ;
            display: flex;
            flex-wrap: wrap;
            gap: 1px;
            justify-content: space-between;
            padding: 4% 2%;
            div{
                position: relative;
                text-align: center;
                font-weight: bold;
                border: 1px solid rgb(119, 115, 115);
                border-radius: 10%;
                background: url('../images/grph.jpg');
                background-repeat: no-repeat;
                background-size:115%;
                height: fit-content;
                opacity: 0.9;
                z-index: 1;
                width: 15vw;
                height: 15vw;
                p{
                    position: relative;
                    opacity: 1;
                    z-index: 2;
                    margin-top: 20%;

                }
                @media (max-width: 768px){
                    height: 30vw;
                    width: 30vw;
                }
            }
            @media (max-width: 768px){
                border-right:none;
                flex-direction: column;
                align-items: center;
            }
            
        }
        .lower{
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            border-right:1px solid grey ;
            padding: 4% 2%;
            @media (max-width: 768px){
                border-right:none;
                flex-direction: column;
                align-items: center;
            }
            div{
                border: 1px solid rgb(119, 115, 115);
                border-radius: 10%;
                height: 15vw;
                width: 15vw;
                @media (max-width: 768px){
                    height: 30vw;
                    width: 30vw;
                }
            }
        }
    }
    .right{
        
        height: 40.5vw;
        width: 35%;
    }
    @media (max-width: 768px) {
        flex-direction: column;
        align-items: center; 
        p{
            font-size: 12px;
        }
        .upper{
            border-right: none;
        }
      }
}