.navbar{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    .container-nav{
        width: 1400px;
        display: flex;
        justify-content: space-between;
        padding: 10px 0px;
        border-bottom: 1px solid black;
        .logo{
            display: flex;
            font-size: 18px;
            gap: 32px;
            .text{
                font-weight: bold;
                :hover{
                    cursor: pointer;
                    color:grey;
                    font-size: 19px;
                }
                .info{
                    font-size: 15px;   
                }
            }
            .dot{
                font-size: 18px;
            }
        }
        .links{
            :hover{
                cursor: pointer;
                color:grey;
            }
            display: flex;
            gap: 24px;
            align-items: center;
        }
    }
}